import React, { Component } from 'react';
import { PropTypes as T } from 'prop-types'
import { Form, FormGroup, FormControl, Button, HelpBlock } from 'react-bootstrap';

export default class NewFactory extends Component {
  
  constructor (props) {
    super(props);
    
    this.state = {
      id: props.edit && props.edit.id || '',
      name: props.edit &&props.edit.name || '',
      upper: props.edit &&props.edit.upper || '',
      lower: props.edit &&props.edit.lower || '',
      children: props.edit && props.edit.children || []
    }
  }
  
  onChange = (e) => {
    const { target } = e;
    const { name, value } = target;
    this.setState({
      [name]: value
    });
  };
  
  validateName = () => {
    const { name } = this.state;
    if (name) return 'success';
    else if (name.length <= 0) return 'error';
    return null;
  };
  
  validateRange = () => {
    const { lower, upper } = this.state;
    if ( (lower && upper) && (lower < upper)) return 'success';
    else if ((lower && upper) && (lower > upper)) return 'error';
    return null;
  };
  
  validateSubmit = () => {
    const { id, name, upper, lower, children } = this.state;
  
    const { onHide, onSubmit } = this.props;
    if ((this.validateRange() === 'success') && (this.validateName() === 'success')) {
      onSubmit({ id, name, upper, lower, children });
      onHide();
    } else {
      return null;
    }
  };
  
  render () {
    const { name, upper, lower } = this.state;
    return (
      <div>
        <Form horizontal
          onSubmit={e => {
            e.preventDefault();
            this.validateSubmit();
          }}
        >
          <FormGroup controlId="formValidationNull" validationState={this.validateName()}>
            <FormControl
              placeholder="Name"
              value={name}
              name="name"
              title="A name is required"
              type="text"
              onChange={this.onChange}
            />
            <FormControl.Feedback />
            <HelpBlock>A name is required</HelpBlock>
          </FormGroup>
          <FormGroup validationState={this.validateRange()}>
            <FormControl
              placeholder="Lower Range"
              name="lower"
              value={lower}
              title="A number for the lower range is required"
              type="text"
              onChange={this.onChange}
            />
            <FormControl.Feedback />
            <HelpBlock>Lower must be less than upper</HelpBlock>
          </FormGroup>
          <FormGroup validationState={this.validateRange()}>
            <FormControl
              placeholder="Upper Range"
              name="upper"
              value={upper}
              title="A number for the upper range is required"
              type="text"
              required
              onChange={this.onChange}
            />
            <FormControl.Feedback />
            <HelpBlock>Upper must be greater than lower</HelpBlock>
          </FormGroup>
            <Button bsStyle="primary" type="submit">Add Factory</Button>
        </Form>
      </div>
    );
  }
}

NewFactory.propTypes = {
  onHide: T.func.isRequired,
  onSubmit: T.func.isRequired,
  edit: T.object
};




